//type rfc
import React from 'react'
//importing props type
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';

export default function Navbar(props) {

    return (
        <div>
            <>
<nav id="nav" className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  <div className="container-fluid">
      {/* props means properties which i want to pass as a argument */}
    <Link className="navbar-brand" to="/textify">{props.title}</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">

      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link to="/textify" className="nav-link active" aria-current="page">Home</Link>
        </li>
        <li className="nav-item">
          <Link to="/about" className="nav-link active" aria-current="page">About</Link>
        </li>
      </ul>
      <form className="d-flex">
      <div className="form-check form-switch">
  <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick= {props.toggleBtn}/>
  <label className="form-check-label" htmlFor="flexSwitchCheckDefault" id="modeText">Enable Dark Theme</label>
</div>
      </form>
    </div>
  </div>
</nav>
</>
        </div>
    )
}

//using prop types to specify proper props used in app
Navbar.propTypes = {
    title: PropTypes.string.isRequired
  }


//if we don't pass any props values in component, then default props will show
Navbar.defaultProps = {
    title: "Add title here"
}